import { ThemeProvider, Global } from "@emotion/react";
import React, { useEffect, useState } from "react";
import ReactDom from "react-dom";
import GeneralMap from "./domains/GeneralMap/GeneralMap";
import GlobalStyle from "./styles/globals";
import theme from "./styles/theme";
import jsonUrl from "url:./data.json";
import { MAP_POINTS_OF_INTEREST } from "./content/map_points";

const App = () => {
  const [parkData, setParkData] = useState(null);
  const transformParkData = (data) => {
    const poi = MAP_POINTS_OF_INTEREST?.map((point) => {
      return { ...point, ...data.points_of_interest[point.id] };
    });

    return { points_of_interest: poi, other_points: data.other_points };
  };
  useEffect(() => {
    fetch(jsonUrl)
      .then((response) => response.json())
      .then((json) => setParkData(transformParkData(json)));
  }, []);

  return parkData ? (
    <>
      <Global styles={GlobalStyle} />
      <ThemeProvider theme={theme}>
        <GeneralMap parkData={parkData} />
      </ThemeProvider>
    </>
  ) : null;
};

ReactDom.render(<App />, document.getElementById("app"));
