import React, { useState } from "react";
import useDetectLanguage from "../../hooks/useDetectLanguage";
import { Card } from "../Card/Card";

export const PointOfInterest = ({
  label,
  description,
  icon,
  mapImage,
  cardImage,
  isHover,
  hidePin,
  shadowPin,
  onPinRelease,
  onPinHover,
  onCardActive,
  onCardClose,
}) => {
  const [showCard, setShowCard] = useState(false);
  const { isArabic } = useDetectLanguage();
  return (
    <>
      {showCard ? (
        <Card
          isArabic={isArabic}
          image={cardImage?.src}
          title={isArabic ? label?.ar : label?.en}
          desc={isArabic ? description?.ar : description?.en}
          onClose={() => {
            setShowCard(false);
            onCardClose();
          }}
          top={cardImage?.position?.y}
          left={cardImage?.position?.x}
        />
      ) : null}

      {typeof mapImage !== "undefined" ? (
        <img
          style={{
            width: mapImage.width,
            height: "auto",
            position: "absolute",
            top: mapImage.position.y,
            left: mapImage.position.x,
            objectFit: "contain",
            transition: "all 0.3s ease-in",
            opacity: isHover || showCard ? "1" : "0",
          }}
          src={mapImage.src}
          alt=""
        />
      ) : null}

      {typeof icon !== "undefined"
        ? React.createElement(icon.src, {
            style: {
              position: "absolute",
              top: `calc(${icon.position.y} - 53px`,
              left: `calc(${icon.position.x} - 44px`,
              cursor: "pointer",
              zIndex: "990",
              opacity: hidePin ? 0 : shadowPin ? 0.6 : 1,
              transition: "all 0.2s ease-in",
              pointerEvents: hidePin ? "none" : "auto",
            },
            onMouseOver: () => {
              onPinHover();
            },
            onMouseLeave: () => {
              onPinRelease();
            },
            onClick: () => {
              onCardActive();
              setShowCard(true);
            },
          })
        : null}
    </>
  );
};
