import React from "react";
import { Checkbox } from "../../components/Checkbox/Checkbox";
import { IconChevronLeft } from "../../components/Icons/IconChevronLeft";
import { OTHER_POINTS_LEGEND } from "../../content/map_other_points";
import { MAP_POINTS_OF_INTEREST } from "../../content/map_points";
import useDetectLanguage from "../../hooks/useDetectLanguage";
import { ALL_OP_KEY, ALL_POI_KEY } from "../../utils/constants/common";
import {
  SCFilter,
  SCFilterButton,
  SCFilterWrapper,
  SCItem,
  SCItemsWrapper,
  SCTitle,
} from "./Filter.style";

export const Filter = ({
  parkData,
  filterState = {},
  onChange,
  isOpen,
  setIsOpen,
  ...props
}) => {
  const isAllPoi = filterState.includes(ALL_POI_KEY);
  const isAllOp = filterState.includes(ALL_OP_KEY);
  const { isArabic } = useDetectLanguage();
  return (
    <SCFilterWrapper isOpen={isOpen} {...props}>
      <SCFilterButton isOpen={isOpen}>
        <IconChevronLeft
          className="icon-chevron"
          onClick={() => setIsOpen(!isOpen)}
        />
      </SCFilterButton>
      <SCFilter>
        <SCItemsWrapper className="points-of-interest" isArabic={isArabic}>
          <SCTitle isArabic={isArabic}>
            <h2>
              {isArabic ? "كمهت ﻲﺘﻟا ﻖﻓاﺮﻤﻟا زﺮﺑأ" : "Points of Interest"}
            </h2>
            <Checkbox
              isChecked={isAllPoi}
              onClick={() => onChange(ALL_POI_KEY)}
            />
          </SCTitle>
          {parkData?.points_of_interest.map((point) => {
            return (
              <SCItem
                key={point?.id}
                onClick={() => onChange(point.id)}
                isArabic={isArabic}
              >
                <div className="content">
                  {typeof point?.filterIcon?.src !== "undefined"
                    ? React.createElement(point?.filterIcon?.src, {
                        className: "icon-left",
                      })
                    : null}
                  <p>{isArabic ? point?.label?.ar : point?.label?.en}</p>
                </div>
                <Checkbox
                  isChecked={filterState.includes(point.id)}
                  onClick={() => onChange(point.id)}
                />
              </SCItem>
            );
          })}
        </SCItemsWrapper>
        <SCItemsWrapper isArabic={isArabic}>
          <SCTitle isArabic={isArabic}>
            <h2>{isArabic ? " ىﺮﺧﺄﻟا ﻖﻓاﺮﻤﻟا" : "Other points"}</h2>
            <Checkbox
              isChecked={isAllOp}
              onClick={() => onChange(ALL_OP_KEY)}
            />
          </SCTitle>
          {Object.values(OTHER_POINTS_LEGEND).map((point) => {
            return (
              <SCItem key={point.id} color={point.color} isArabic={isArabic}>
                <div className="content other-points">
                  <span className="point" />
                  <p>{isArabic ? point?.label?.ar : point?.label?.en}</p>
                  {typeof point?.icon !== "undefined"
                    ? React.createElement(point?.icon)
                    : null}
                </div>
                <Checkbox
                  isChecked={filterState.includes(point.id)}
                  onClick={() => onChange(point.id)}
                />
              </SCItem>
            );
          })}
        </SCItemsWrapper>
      </SCFilter>
    </SCFilterWrapper>
  );
};
