import React, { useState } from "react";
import { OTHER_POINTS_LEGEND } from "../../content/map_other_points";
import useDetectLanguage from "../../hooks/useDetectLanguage";
import { RestaurantCard } from "../RestaurantCard/RestaurantCard";

export const Pin = ({ pinInfo, onHover, onRelease, hidePin, shadowPin }) => {
  const [showLabel, setShowLabel] = useState(false);
  const { isArabic } = useDetectLanguage();
  return (
    <>
      {typeof OTHER_POINTS_LEGEND[pinInfo.type]?.pin !== "undefined"
        ? React.createElement(OTHER_POINTS_LEGEND[pinInfo.type]?.pin, {
            style: {
              position: "absolute",
              zIndex: "990",
              top: `calc(${pinInfo.pos.y} - 45px`,
              left: `calc(${pinInfo.pos.x} - 38px`,
              cursor: pinInfo.label ? "pointer" : "default",
              pointerEvents: hidePin ? "none" : "auto",
              opacity: hidePin ? 0 : shadowPin ? 0.5 : 1,
              transition: "all 0.2s ease-in",
            },
            onMouseEnter: () => {
              onHover(pinInfo.id);
              pinInfo.label && setShowLabel(true);
            },
            onMouseLeave: () => {
              onRelease();
              pinInfo.label && setShowLabel(false);
            },
          })
        : null}
      {showLabel ? (
        <RestaurantCard
          isArabic={isArabic}
          title={isArabic ? pinInfo?.label?.ar : pinInfo?.label?.en}
          style={{
            top: `calc(${pinInfo.pos.y} - 120px`,
            left: `calc(${pinInfo.pos.x} - 30px`,
          }}
        />
      ) : null}
    </>
  );
};
