import React, { useEffect, useState } from "react";
import { SCMainContainer } from "./GeneralMap.style";
import park_general from "../../../src/public/images/Umm_Al_Emarat_Park_Illustration_full.png";
import { withOrientationChange, isMobile } from "react-device-detect";
import { Pin } from "../../components/Pin/Pin";
import { PointOfInterest } from "../../components/PointOfInterest/PointOfInterest";
import { OTHER_POINTS_LEGEND } from "../../content/map_other_points";
import { Filter } from "../Filter/Filter";
import { ALL_OP_KEY, ALL_POI_KEY } from "../../utils/constants/common";
import { SCOverlay } from "../../components/Overlay/Overlay.style";
import { OnlyPortraitAdvise } from "../OnlyPortraitAdvise/OnlyPortraitAdvise";

const GeneralMap = (props) => {
  const { isLandscape, parkData } = props;
  const initialFilterState = [
    ...Object.values(parkData?.points_of_interest).map((point) => point.id),
    ...Object.values(OTHER_POINTS_LEGEND).map((point) => point.id),
    ALL_OP_KEY,
    ALL_POI_KEY,
  ];
  const [filterState, setFilterState] = useState(initialFilterState);
  const [otherPointsVisible, setOtherPointsVisible] = useState(
    parkData?.other_points
  );
  const [pointOfInterestVisible, setPointOfInterestVisible] = useState(
    parkData?.points_of_interest
  );

  const [isFilterOpen, setIsFilterOpen] = useState(false);

  const [activeCard, setActiveCard] = useState(null);
  const [activePin, setActivePin] = useState(null);
  const [hoverPin, setHoverPin] = useState(null);
  const handleFilterChange = (id) => {
    const isAllOp = id === ALL_OP_KEY;
    const isAllPoi = id === ALL_POI_KEY;

    const filterByCategory = (option, areOtherPoints) =>
      !Object.values(
        areOtherPoints ? OTHER_POINTS_LEGEND : parkData?.points_of_interest
      )
        .map((opt) => opt.id)
        .includes(option) && option !== (isAllOp ? ALL_OP_KEY : ALL_POI_KEY);

    if (filterState.includes(id)) {
      if (isAllOp || isAllPoi) {
        const nextState = [...filterState].filter((option) =>
          filterByCategory(option, isAllOp)
        );
        setFilterState(nextState);
        return;
      }

      const nextState = [...filterState].filter((_id) => _id !== id);
      setFilterState(nextState);
      return;
    }
    if (isAllOp || isAllPoi) {
      const nextState = [
        ...filterState,
        ...Object.values(
          isAllOp ? OTHER_POINTS_LEGEND : parkData?.points_of_interest
        ).map((point) => point.id),
        `${isAllOp ? ALL_OP_KEY : ALL_POI_KEY}`,
      ];
      setFilterState(nextState);
      return;
    }
    setFilterState([...filterState, id]);
  };

  useEffect(() => {
    const nextOtherPoints = [...parkData?.other_points].filter((point) =>
      filterState.includes(point.type)
    );
    setOtherPointsVisible(nextOtherPoints);

    const nextPointsOfInterest = [...parkData?.points_of_interest].filter(
      (point) => filterState.includes(point.id)
    );
    setPointOfInterestVisible(nextPointsOfInterest);
  }, [filterState]);

  return parkData ? (
    <SCMainContainer>
      {isMobile && !isLandscape ? (
        <OnlyPortraitAdvise />
      ) : (
        <>
          <Filter
            parkData={parkData}
            isOpen={isFilterOpen}
            setIsOpen={(state) => setIsFilterOpen(state)}
            onChange={(id) => handleFilterChange(id)}
            filterState={filterState}
          />
          <img src={park_general} alt="" />
          <SCOverlay showFilter={activeCard || activePin || hoverPin} />
          {otherPointsVisible &&
            otherPointsVisible.map((point, idx) => {
              return (
                <Pin
                  key={`${point.type}-${idx}`}
                  pinInfo={point}
                  onHover={() => {
                    setActivePin(point.id);
                  }}
                  onRelease={() => setActivePin(null)}
                  hidePin={activeCard}
                  shadowPin={(activePin && activePin !== point.id) || hoverPin}
                />
              );
            })}
          {pointOfInterestVisible &&
            pointOfInterestVisible.map((point) => {
              return (
                <PointOfInterest
                  key={point.id}
                  {...point}
                  hidePin={activeCard}
                  shadowPin={(hoverPin && hoverPin !== point.id) || activePin}
                  isHover={hoverPin === point.id}
                  onPinRelease={() => setHoverPin(null)}
                  onPinHover={() => setHoverPin(point.id)}
                  onCardActive={() => {
                    setActiveCard(point.id);
                    setIsFilterOpen(false);
                  }}
                  onCardClose={() => setActiveCard(null)}
                />
              );
            })}
          <PointOfInterest />
        </>
      )}
    </SCMainContainer>
  ) : null;
};

export default withOrientationChange(GeneralMap);
