import React from "react";
import { IconCheck } from "../Icons/IconCheck";
import { SCCheckbox, SCEmptyCheck } from "./Checkbox.style";

export const Checkbox = ({ isChecked, onClick, ...props }) => {
  return (
    <SCCheckbox onClick={() => onClick && onClick()} {...props}>
      {isChecked ? <IconCheck /> : <SCEmptyCheck />}
    </SCCheckbox>
  );
};
