import React, { useRef } from "react";
import {
  CardDesc,
  CardTitle,
  SCCard,
  SCImageWrapper,
  SCInfoContainer,
} from "./Card.style";
import { IconClose } from "../Icons/IconClose";
import useOnClickOutside from "../../hooks/useOnClickOutside";
export const Card = ({ image, title, desc, isArabic, onClose, ...props }) => {
  const node = useRef(null);
  useOnClickOutside(node, onClose);

  return (
    <SCCard {...props} ref={node}>
      <IconClose onClick={() => onClose()} />
      <SCImageWrapper>{image ? <img src={image} /> : null}</SCImageWrapper>
      <SCInfoContainer isArabic={isArabic}>
        <CardTitle isArabic={isArabic}>{title}</CardTitle>
        <CardDesc isArabic={isArabic}>{desc}</CardDesc>
      </SCInfoContainer>
    </SCCard>
  );
};
